import request from '@/utils/request'
import { ListQuery } from '@/domains/listQuery'
import { Syslisence } from '@/domains/syslisence'

export function fetchList(query: ListQuery) {
  return request({
    url: '/sys/syslisence',
    method: 'get',
    params: query
  })
}

export function saveData(data: Syslisence) {
  return request({
    url: '/sys/syslisence',
    method: 'post',
    data
  })
}

export function fetchEditInfo(id: string) {
  const url = '/sys/syslisence/' + id
  return request({
    url,
    method: 'get'
  })
}

export function updateData(data: Syslisence) {
  return request({
    url: '/sys/syslisence',
    method: 'put',
    data
  })
}

export function deleteData(data: string[]) {
  return request({
    url: '/sys/syslisence',
    method: 'delete',
    data
  })
}

export function toGrant(id: string, params: any) {
  return request({
    url: '/sys/syslisence/grant/' + id,
    method: 'put',
    params
  })
}
