/** 排序条件 */
export default class Syslisence {
  public static build(data: Syslisence) {
    const syslisence: Syslisence = new Syslisence()
    syslisence.lisenceId = data.lisenceId
    syslisence.privatekey = data.privatekey
    syslisence.publickey = data.publickey
    syslisence.privatekey2 = data.privatekey2
    syslisence.publickey2 = data.publickey2
    syslisence.systemuid = data.systemuid
    syslisence.signkey = data.signkey
    syslisence.applydate = data.applydate
    syslisence.grantdate = data.grantdate
    syslisence.usingtype = data.usingtype
    syslisence.serverHost = data.serverHost
    syslisence.systemName = data.systemName
    syslisence.dueDays = data.dueDays
    syslisence.serverUrl = data.serverUrl
    return syslisence
  }

  /** lisenceId */
  public lisenceId: string = ''
  /** 私钥 */
  public privatekey: string = ''
  /** 公钥 */
  public publickey: string = ''
  /** 私钥2 */
  public privatekey2: string = ''
  /** 公钥2 */
  public publickey2: string = ''
  /** 系统唯一标识 */
  public systemuid: string = ''
  /** 授权key */
  public signkey: string = ''
  /** 申请日期 */
  public applydate: string = ''
  /** 授权日期 */
  public grantdate: string = ''
  /** 使用类型 */
  public usingtype: string = ''
  /** 服务器IP */
  public serverHost: string = ''
  /** 系统名称 */
  public systemName: string = ''
  /** 授权期限 */
  public dueDays: number = 0
  /** 服务完整URL */
  public serverUrl: string = ''
  /** 索引 */
  readonly [id: string]: string | number
}
