




























































import { saveData, fetchEditInfo, updateData } from '@/api/sysManage/syslisenceManage'
import { Component, Vue, Prop } from 'vue-property-decorator'
import { Syslisence } from '@/domains/syslisence'

@Component({
  name: 'SyslisenceEdit'
})
export default class Edit extends Vue {
  @Prop()
  private id!: string
  @Prop({ default: 'save' })
  private type!: string
  /** form数据 */
  private ruleForm: Syslisence = new Syslisence()
  /** form校验 */
  private rules: any = {
    key: [
      { required: true, message: '请输入参数名称', trigger: 'blur' }
    ],
    value: [
      { required: true, message: '请输入参数值', trigger: 'blur' }
    ]
  }
  private created() {
    if (this.type === 'edit') {
      this.getEditInfo()
    }
  }
  // 校验提交
  private submitForm(formName: string) {
    let refs: any = this.$refs[formName]
    refs.validate((valid: boolean) => {
      if (valid) {
        if (this.type === 'edit') {
          this.handleEdit()
        } else {
          this.handleSubmit()
        }
      } else {
        console.log('error submit!!')
        return false
      }
    })
  }
  // 保存
  private handleSubmit() {
    saveData(this.ruleForm).then(response => {
      this.$emit('msg-from-child', this.type)
    })
  }
  // 修改
  private handleEdit() {
    updateData(this.ruleForm).then(response => {
      this.$emit('msg-from-child', this.type)
    })
  }
  // 取消
  private cancel() {
    this.$emit('msg-from-child', 'cancel')
  }
  // 获取编辑信息
  private getEditInfo(): void {
    fetchEditInfo(this.id).then((response: any) => {
      const data: Syslisence = response.sysLisence
      this.ruleForm = Syslisence.build(data)
    }).catch(err => {
      console.log(err)
    })
  }
}
